<template>
  <div id="root">
    <div class="title"><span>选择贴纸</span></div>
    <div class="description">选择一个你喜欢的贴纸吧！</div>
    <div id="imgBox">
      <ul class="imgUl">
        <li v-for="(url, index) in stickerList" :key="index">
          <img :src="url" @click="addImgLayers(url)" style="cursor:pointer"/>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import stick1 from '@/assets/sticker/1.png'
import stick2 from '@/assets/sticker/2.png'
import stick3 from '@/assets/sticker/3.png'
import stick4 from '@/assets/sticker/4.png'
import stick5 from '@/assets/sticker/5.png'
import stick6 from '@/assets/sticker/6.png'
import stick7 from '@/assets/sticker/7.png'
import stick8 from '@/assets/sticker/8.png'
import stick9 from '@/assets/sticker/9.png'
import stick10 from '@/assets/sticker/10.png'
import stick11 from '@/assets/sticker/11.png'
import stick12 from '@/assets/sticker/12.png'
import stick13 from '@/assets/sticker/13.png'
import stick14 from '@/assets/sticker/14.png'
import stick15 from '@/assets/sticker/15.png'
export default {
  name: 'sticker',
  created () {},
  data () {
    return {
      stickerList: [stick1, stick2, stick3, stick4, stick5, stick6, stick7, stick8, stick9, stick10, stick11, stick12, stick13, stick14, stick15]
    }
  },
  props: [],
  methods: {
    // 向画布添加素材
    addImgLayers (url) {
      this.$store.state.canvasData.addLayer('image', url)
    }
  }
}
</script>

<style scoped>
#root {
  /* margin: 2px 0 0 2px; */
  /* border: 1px solid #d6d7de; */
  width: 23.86vw;
  height: 85vh;
}
.title {
  width: 18vw;
  height: 4.4vh;
  background-color: #f5c2ca;
  border-radius: 1vh;
  margin: 0 auto;
  margin-top: 3.15vh;
  display: flex;
  justify-content: center;
}
.title > span {
  align-self: center;
}
.description {
  text-align: center;
  font-size: 0.57vw;
  color: #767676;
}

/* 贴纸样式开始 */
#imgBox {
  width: 23.86vw;
  height: 83vh;
  margin-top: 0.633vh;
  border-top: 1px solid #d6d7de;
  overflow-y: scroll;
}
::-webkit-scrollbar {
  display: none; /* Chrome Safari */
}

.imgUl {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
}
.imgUl > li {
  width: 5.76vw;
  height: 5.76vw;
  list-style: none;
  margin-left: 1.55vw;
  margin-top: 3.16vh;
  position: relative;
}
.imgUl > li > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* 贴纸样式结束 */
</style>



